import React, { useRef, useCallback } from 'react';
import SwiperCore, {
  Pagination, Scrollbar, A11y, Autoplay,
} from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import { randomId } from '../utils/common';
import ActualOffer from './actual-offer';
import ActualOffersControls from './actual-offers-controls';

const ActualOffersList = () => {
  const swiperRef = useRef(null);
  SwiperCore.use([Pagination, Scrollbar, A11y, Autoplay]);

  const prevSlide = useCallback(() => {
    swiperRef.current?.swiper.slidePrev();
  }, [swiperRef]);

  const nextSlide = useCallback(() => {
    swiperRef.current?.swiper.slideNext();
  }, [swiperRef]);

  return (
    <>
      <ActualOffersControls prevSlide={prevSlide} nextSlide={nextSlide} />
      <ul className="actual-offers__list navigation">
        <Swiper
          ref={swiperRef}
          pagination={{ clickable: true }}
          scrollbar={{ draggable: true }}
          spaceBetween={50}
          slidesPerView={3}
          loop
        >
          {new Array(10).fill().map(() => (
            <SwiperSlide key={randomId()}>
              <ActualOffer />
            </SwiperSlide>
          ))}
        </Swiper>
      </ul>
    </>
  );
};

export default ActualOffersList;
