import React, { useEffect, useRef } from 'react';
import { css } from '@emotion/react';
import Typed from 'react-typed';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { ENTER_KEY_CODE } from '../../constants/keys';
import mapStateToPropsGenerator from '../../store/mapStateToProps';
import components from '../../constants/components';
import SearchIcon from '../../icons/search-icon.svg';
import { searchShowing } from '../utils/animation';
import SectionInner from '../../containers/section-inner';
import Select from '../utils/select';
import Hints from './hints';

const SearchModal = ({ hints }) => {
  const searchRef = useRef();

  const placeValueToSearch = (evt) => {
    evt.preventDefault();
    searchRef.current.value = evt.currentTarget.textContent;
  };

  useEffect(() => {
    searchRef.current.focus();
  }, []);

  return (
    <section
      className="header__modal-search seciton search-modal"
      css={css`
        animation: ${searchShowing} 2s ease-in-out;
      `}
    >
      <SectionInner>
        <div className="search-modal__container">
          <div className="search-modal__container-top">
            <h2 className="search-modal__title">
              Простой и удобный поиск
              {' '}
              <br className="search__title-br" />
              <Typed
                strings={['Анализов^5000', 'Услуг^5000', 'Врачей^5000']}
                typeSpeed={50}
                backSpeed={50}
                loop
              >
                <span />
              </Typed>
            </h2>
            <small className="search-modal__container-top-text">По какому разделу искать?</small>
            <div className="search-modal__container-input-group">
              <input
                type="text"
                className="search-modal__input"
                placeholder="Введите ваш поисковый запрос…"
                aria-label="Введите что хотите найти"
                aria-describedby="search-descr"
                ref={searchRef}
              />
              <p className="visually-hidden" id="search-descr">
                Введите ваш поисковый запрос…
              </p>
              <Select selectClass="search-modal__categories" />
              <button
                className="search-modal__button"
                type="button"
                name="search-button"
                aria-label="Кнопка поиска"
              >
                <SearchIcon
                  width="25px"
                  height="25px"
                  transform="scale(1.2)"
                  fill="none"
                  stroke="currentColor"
                />
              </button>
            </div>
            <small className="search-modal__hint">
              <SearchIcon
                width="25px"
                height="25px"
                transform="scale(0.8)"
                fill="none"
                stroke="currentColor"
                className="search-modal__hint-icon"
              />
              Например,
              <Typed
                strings={[
                  'биохимический анализ крови^5000',
                  'анализ на ковид^5000',
                  'анализ на анализ^5000',
                ]}
                typeSpeed={50}
                backSpeed={50}
                loop
              >
                <span
                  className="search-modal__hint-dotted"
                  onClick={(evt) => {
                    placeValueToSearch(evt);
                  }}
                  role="button"
                  onKeyDown={(evt) => {
                    if (evt.key === ENTER_KEY_CODE) placeValueToSearch(evt);
                  }}
                  tabIndex="0"
                  aria-label="Поместить это текст в строку поиска"
                />
              </Typed>
            </small>
            <Hints hints={hints} />
          </div>
        </div>
      </SectionInner>
    </section>
  );
};

SearchModal.propTypes = {
  hints: PropTypes.arrayOf(PropTypes.object).isRequired,
};

export default connect(mapStateToPropsGenerator(components.HINTS), null)(SearchModal);
